<script>
  export let id;
  export let label;
  export let value = "";
  export let textarea = false;
  export let rows = "3";
</script>

<style>
  label {
    position: relative;
    margin: 0;
    padding: 0;
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    background: white;
    border-bottom: 1px solid #222;
  }

  label:last-child {
    border-bottom: none;
  }

  input[type="text"],
  textarea {
    box-sizing: border-box;
    display: block;
    margin: 0px;
    padding: 20px 5px 5px;
    border: 0;
    border-radius: 0;
    width: 100%;
    height: auto;
    min-height: 45px;
    resize: vertical;
    font: 12px / 1.5 Courier, monospace;
    text-align: left;
    color: #444;
    background: transparent;
  }

  textarea {
    min-height: 80px;
  }

  span {
    position: absolute;
    display: block;
    height: 10px;
    width: -webkit-fill-available;
    width: -moz-available;
    width: fill-available;
    margin: 0 !important;
    padding: 5px !important;
    border: 0;
    border-radius: 0;
    font: italic 9px / 1.5 Georgia, serif !important;
    text-align: left;
    background: transparent;
  }

/*# sourceMappingURL=data:application/json;base64, */</style>

<label>
  <span>{label}</span>
  {#if textarea}
    <textarea name="attr_{id}" {value} {rows} />
  {:else}
    <input type="text" name="attr_{id}" {value} autocomplete="new-password" />
  {/if}
</label>
