<script>
  import Toggle from "./fields/SheetToggle.svelte";

  export let id;
  export let label = false;
  export let boxed = false;
  export let hasToggle = false;

  export let col = 1;
  export let row = 1;
</script>

<style>
  div {
    position: relative;
    padding-top: 4px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content
  }

  div.sheet-boxed {
      padding-bottom: 4px;
      border: 2px solid #222;
    }

  div::before,
    div.sheet-boxed::after {
      content: "";
      box-sizing: border-box;
      position: absolute;
      top: -2px;
      left: 0;
      display: block;
      width: 100%;
      height: 6px;
      border-top: 2px solid #222;
      border-bottom: 2px solid #222;
      background-color: white;
    }

  div.sheet-boxed::after {
      top: auto;
      top: initial;
      bottom: -2px;
    }

  div:not(.sheet-boxed) {
      margin-top: 2px;
    }

  div:not(.sheet-boxed)::before {
      border: 2px solid #222;
    }

  div > div {
    margin: 0;
    padding: 0
  }

  div > div::before {
      content: none;
    }

/*# sourceMappingURL=data:application/json;base64, */</style>

<div class:sheet-boxed={boxed} style="grid-column: {col}; grid-row: {row};">
  {#if label && hasToggle}
    <Toggle hidden {id} />
    <h2>
      {label}
      <Toggle style="arrow" {id} />
    </h2>
    <div class="sheet-drawer">
      <slot />
    </div>
  {:else if label && !hasToggle}
    <h2>{label}</h2>
    <slot />
  {/if}

</div>
