<script>
  export let style = false;
  export let id;
  export let label = "";
  export let checked = false;
  export let hidden;
</script>

<style global>
  :global(input[type="checkbox"]) {
    display: inline-block;
    margin-right: -14px;
    width: 10px;
    height: 10px;
    opacity: 0;
  }

  :global(input[type="checkbox"] + span::before) {
    display: inline-block;
    vertical-align: middle;
    content: "";
    width: 6px;
    height: 6px;
    margin-right: 5px;
    border: 2px solid #222;
    border-radius: 100%;
  }

  :global(input[type="checkbox"]:checked + span::before) {
    background: #222;
  }

  :global(input[type="checkbox"].sheet-arrow) {
    margin-left: 3px
  }

  :global(input[type="checkbox"].sheet-arrow + span::before) {
      content: none;
    }

  :global(input[type="checkbox"].sheet-arrow + span::after) {
      content: "▼";
      display: inline-block;
      padding: 3px;
      width: 10px;
      height: 10px;
    }

  :global(input[type="checkbox"].sheet-arrow:checked + span::after) {
      content: "►";
    }
/*# sourceMappingURL=data:application/json;base64, */</style>

{#if hidden}
  <input
    type="hidden"
    class="sheet-toggle"
    name="attr_{id}-toggle"
    value={checked ? 'hidden' : '0'} />
{:else}
  <input
    class="sheet-{style}"
    type="checkbox"
    name="attr_{id}-toggle"
    checked={checked ? 'checked' : null} />
  <span>{label}</span>
{/if}
