<script>
  export let mode;
  export let title;
  export let checked = false;
</script>

<style>
  input[type="radio"] {
    display: inline-block;
    margin-right: -14px;
    width: 10px;
    height: 10px;
    opacity: 0;
  }

  input[type="radio"] + span::before {
    display: inline-block;
    vertical-align: middle;
    content: "";
    width: 6px;
    height: 6px;
    margin-right: 5px;
    border: 2px solid black;
    border-radius: 100%;
  }

  input[type="radio"]:checked + span::before {
    background: black;
  }

/*# sourceMappingURL=data:application/json;base64, */</style>

<input
  type="radio"
  name="attr_sheet-mode"
  value={mode}
  checked={checked ? 'checked' : null} />
<span>{title}</span>
